@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  background-color: #050505;
  color: white;
  overflow: hidden;
}

#root {
  width: 100vw;
  height: 100vh;
}

.splashscreen {
  display: none;
}

.App {
  width: 100%;
  height: 100%;
}

.header {
  position: fixed;
  box-sizing: border-box;
  width: 100%;
  padding: 20px;
}

.header h3 {
  font-size: 24pt;
  font-weight: 600;
}

.header ul {
  padding-left: 20px;
  font-size: 18pt;
}

.header ul li {
  list-style: none;
  font-weight: 200;
}

.header strong {
  font-weight: 400;
}

.header h2, img {
  display: inline-block;
  vertical-align: middle;
}

.header img {
  width: 150px;
}

.header h2 {
  font-weight: 900;
  font-size: 48pt;
}

#course {
  margin-top: 20px;
}

#current-turn {
  color: #43d300;
  font-weight: 600;
  font-size: 28pt;
  text-align: center;
}

#next-turn {
  color: white;
  font-weight: 600;
  font-size: 28pt;
  text-align: center;
  opacity: 0.5;
}

.container {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

#ctx {
  background-color: black;
}

.footer {
  position: fixed;
  box-sizing: border-box;
  bottom: 0;
  padding: 20px;
  width: 100%;
}

.footer h3 {
  font-size: 24pt;
  font-weight: 600;
}

.footer ul {
  padding-left: 20px;
  font-size: 18pt;
}

.footer ul li {
  list-style: none;
  font-weight: 200;
}

.footer strong {
  font-weight: 400;
}

.left {
  float: left;
}

.right {
  float: right;
  width: 200px;
}

.right h3 {
  text-align: left;
}

@media only screen and (max-width: 1023px) {
  .App {
    display: none;
  }

  .splashscreen {
    display: flex !important;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background-color: #101050;
    color: white;
  }

  .splashscreen img {
    width: 150px;
  }

  .splashscreen h3 {
    font-weight: 200;
  }

  .splashscreen h3 strong {
    font-weight: 500;
  }
}